.tree-widget-visibility-widget {
  padding: 8px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.tree-widget-visibility-widget .components-selectable-content {
  flex: 1;
}
.tree-widget-visibility-widget .components-selectable-content-header {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}
.tree-widget-visibility-widget .components-selectable-content-header .uicore-reactSelectTop {
  flex: 1;
}
.tree-widget-visibility-widget .components-selectable-content-wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
}
.tree-widget-visibility-widget .visibility-tree-toolbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 4px;
}
.tree-widget-visibility-widget .visibility-tree-toolbar .tree-toolbar-searchbox {
  margin-left: auto;
}
.tree-widget-visibility-widget .visibility-tree-toolbar .tree-toolbar-searchbox .components-filtering-input {
  margin: 0;
}

.tree-widget-select {
  margin-bottom: 4px;
}

.tree-widget-tree-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.tree-widget-search-bar {
  margin-bottom: 8px;
}