/** Widget Opacity */
.nz-footer-dialog-button {
  color: var(--buic-foreground-muted);
  cursor: pointer;
  position: relative;
}
.nz-footer-dialog-button:hover {
  color: var(--buic-foreground-activehover);
}
.nz-footer-dialog-button:active {
  color: var(--buic-foreground-activehover);
}
.nz-footer-dialog-button:focus {
  outline: none;
}