.tree-widget-searchbox {
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  display: flex;
  align-items: center;
  position: relative;
  height: 34px;
  width: 12em;
  font-size: 14px;
  color: var(--buic-foreground-body-reverse);
  background: var(--buic-background-control);
  border: 1px solid var(--buic-inputs-border);
}
.tree-widget-searchbox input {
  padding: 0 8px;
  height: 100%;
  width: 100%;
  flex: 1;
  background: var(--buic-background-control);
  margin-right: 8px;
  color: var(--buic-foreground-body);
  font-size: inherit;
  box-sizing: border-box;
  border: 0;
}
.tree-widget-searchbox input::placeholder {
  color: var(--buic-foreground-disabled);
}
.tree-widget-searchbox input:focus-within, .tree-widget-searchbox input:focus, .tree-widget-searchbox input:active, .tree-widget-searchbox input[disabled], .tree-widget-searchbox input[disabled]:hover, .tree-widget-searchbox input[disabled]:active, .tree-widget-searchbox input[disabled]:focus {
  border: 0;
  outline: none;
  outline-width: 0;
  box-shadow: none;
}
.tree-widget-searchbox .searchbox-stepping-container {
  display: flex;
  align-items: center;
  right: 32px;
  height: 100%;
}
.tree-widget-searchbox .searchbox-stepping-container .searchbox-stepping-count {
  font-size: 12px;
  color: var(--buic-foreground-body);
  margin-right: 4px;
  position: relative;
  word-wrap: none;
}
.tree-widget-searchbox .searchbox-stepping-container .searchbox-separator {
  height: 75%;
  width: 1px;
  margin: 0 8px;
  background: var(--buic-inputs-border);
}
.tree-widget-searchbox .searchbox-step-button {
  height: 100%;
  width: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  color: var(--buic-foreground-body);
  font-size: 12px;
  cursor: pointer;
}
.tree-widget-searchbox .searchbox-step-button:hover {
  color: var(--buic-accessory-primary);
}
.tree-widget-searchbox .searchbox-close-button {
  z-index: 1000;
}