.property-grid-react-element-list {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.property-grid-react-element-list-header {
  display: flex;
  align-items: center;
  height: 50px;
  box-sizing: border-box;
  margin-left: 6px;
  margin-right: 6px;
}

.property-grid-react-element-list-back-btn {
  cursor: pointer;
  display: flex;
  padding: 4px;
  margin-right: 2px;
}

.property-grid-react-element-list-icon {
  color: var(--buic-foreground-body);
  font-size: 24px;
}
.property-grid-react-element-list-icon:hover {
  color: var(--buic-foreground-primary);
}

.property-grid-react-element-list-title {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--buic-foreground-body);
}

.property-grid-react-element-list-container {
  flex: 1;
  overflow: auto;
}
.property-grid-react-element-list-container::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
.property-grid-react-element-list-container::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background-color: var(--buic-background-scrollbar);
  background-clip: content-box;
  border-style: solid;
  border-color: transparent;
  border-width: 2px;
}
.property-grid-react-element-list-container::-webkit-scrollbar-thumb:hover {
  background-color: var(--buic-background-scrollbar-hover);
}
.property-grid-react-element-list-container::-webkit-scrollbar-corner {
  background-color: transparent;
}
.property-grid-react-element-list-container::-webkit-scrollbar-track-piece:corner-present {
  margin-bottom: -4px;
  margin-right: -4px;
}