.uifw-view-overlay {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: calc(100% - 80px);
  height: auto;
  position: absolute;
  bottom: 10px;
  left: 40px;
  right: 40px;
  pointer-events: none;
  z-index: 1000;
}

.uifw-animation-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 1000px;
  height: auto;
  background: var(--buic-background-dialog);
  border: 1px solid var(--buic-background-dialog-stroke);
  border-radius: 4px;
  opacity: 0.9;
  pointer-events: auto;
}
.uifw-animation-overlay:hover {
  opacity: 1;
}