.nz-zones-outline {
  padding: 5px;
  box-sizing: border-box;
  position: absolute;
  z-index: 8000;
}
.nz-zones-outline:after {
  content: "";
  width: 100%;
  height: 100%;
  box-shadow: 0 0 5px 5px rgb(140, 210, 255), 0 0 5px 5px rgb(140, 210, 255) inset;
  display: block;
}