/** Widget Opacity */
.nz-toolSettings-docked {
  height: 2.5em;
  width: 100%;
  display: flex;
  position: relative;
  background: var(--buic-background-widget);
  border: 1px solid var(--buic-background-widget-stroke);
}
.nz-toolSettings-docked > .nz-container {
  display: flex;
}

.nz-toolSettings-docked_container {
  display: grid;
  grid-template-columns: auto 1fr;
  row-gap: 4px;
  column-gap: 4px;
  align-items: flex-start;
}

.toolsettings-appear {
  opacity: 0.1;
  transition: opacity 150ms ease-in-out;
  transform: translateY(-100%);
}

.toolsettings-appear-active {
  opacity: 0.7;
  transition: opacity 150ms ease-in-out;
  transform: translateY(0);
  transition: transform 400ms ease-out;
}