/** Widget Opacity */
.nz-backstage-userProfile {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-bottom: 14px;
  padding-top: 14px;
}
.nz-backstage-userProfile > span:first-child {
  flex: initial;
  font-size: 1em;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  margin-left: 24px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.nz-backstage-userProfile > span:last-child {
  flex: 1;
  margin-left: 6px;
  margin-right: 6px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.nz-backstage-userProfile.nz-safe-area-left {
  /* iOS 11 */
  padding-left: constant(safe-area-inset-left);
  /* iOS 11.2+ */
  padding-left: env(safe-area-inset-left);
}
.nz-backstage-userProfile:hover {
  background: var(--buic-row-hover);
  color: var(--buic-foreground-body);
}
.nz-backstage-userProfile:focus {
  outline: none;
}