.uifw-stagepanelheader {
  align-items: center;
  height: 36px;
  justify-content: flex-end;
  padding: 5px 10px;
  box-sizing: border-box;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr auto;
}
.uifw-stagepanelheader > .uifw-collapse {
  cursor: pointer;
  justify-self: end;
  padding: 5px;
}
.uifw-stagepanelheader > .uifw-collapse:focus {
  outline: none;
}