/* Breakpoints */
.nz-widget-tab {
  user-select: none;
  white-space: nowrap;
  display: flex;
  gap: 6px;
  align-items: center;
  cursor: pointer;
  padding: 4px 1em 0;
  position: relative;
  border: 0 solid var(--buic-background-5);
  max-width: 9em;
  background: var(--buic-background-3);
}
.nz-widget-tab > span {
  font-size: 0.9em;
  text-overflow: ellipsis;
  overflow: hidden;
}
.nz-widget-tab > .nz-badge {
  position: absolute;
  top: 0;
  left: 0;
}
.nz-widget-tab:not(.nz-minimized).nz-active {
  background: var(--buic-background-2);
  color: var(--buic-foreground-primary);
  padding-bottom: 1px;
  margin-bottom: -1px;
}
.nz-widget-tab:not(.nz-minimized).nz-active:before {
  content: "";
  position: absolute;
  background: currentColor;
  left: 0.25em;
  top: 1px;
  border-radius: 0.2em;
  width: calc(100% - 0.5em);
  height: 0.15em;
}
.nz-widget-tab:not(.nz-minimized).nz-active.nz-last {
  border-right-width: 1px;
}
.nz-widget-tab:not(.nz-active), .nz-widget-tab.nz-minimized {
  color: var(--buic-foreground-muted);
  border-right-width: 1px;
}
.nz-widget-tab:not(.nz-minimized).nz-first-inactive {
  border-left-width: 1px;
}
.nz-widget-tab.nz-first {
  border-top-left-radius: 2px;
}
.nz-widget-tab:hover {
  background: var(--buic-background-2);
}
.nz-widget-tab:hover:not(.nz-active) {
  color: var(--buic-foreground-body);
}