.uifw-statusFields-promptField {
  overflow: hidden;
}
.uifw-statusFields-promptField.nz-footer-mode {
  width: 20em;
}
.uifw-statusFields-promptField:not(.nz-footer-mode) {
  width: 16em;
}
@media only screen and (max-width: 768px) {
  .uifw-statusFields-promptField {
    font-size: 12px;
  }
  .uifw-statusFields-promptField.nz-footer-mode {
    width: 12em;
  }
  .uifw-statusFields-promptField:not(.nz-footer-mode) {
    width: 10em;
  }
}
@media only screen and (max-width: 600px) {
  .uifw-statusFields-promptField {
    font-size: 12px;
  }
  .uifw-statusFields-promptField.nz-footer-mode {
    width: 10em;
  }
  .uifw-statusFields-promptField:not(.nz-footer-mode) {
    width: 8em;
  }
}