.tree-widget-icon-button {
  display: inline-block;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border: 0;
  color: var(--buic-foreground-body);
  background-color: transparent;
  outline: none;
  box-shadow: none;
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  transition: color 0.25s;
}
.tree-widget-icon-button:hover {
  text-decoration: none;
  color: var(--buic-foreground-primary);
}
.tree-widget-icon-button:active {
  outline: none;
  box-shadow: none;
  text-decoration: none;
  background-color: transparent;
}
.tree-widget-icon-button:focus {
  outline: none;
  box-shadow: none;
  text-decoration: none;
  background-color: transparent;
}
.tree-widget-icon-button[disabled], .tree-widget-icon-button[disabled]:hover, .tree-widget-icon-button[disabled]:active, .tree-widget-icon-button[disabled]:focus {
  opacity: 0.4;
  color: #9ba5ae;
  background: transparent;
  cursor: not-allowed;
  pointer-events: none;
}

.tree-widget-icon-label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 32px;
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  color: var(--buic-foreground-body);
  margin-left: 5px;
}
.tree-widget-icon-label[disabled] {
  opacity: 0.4;
  color: #9ba5ae;
  background: transparent;
  cursor: not-allowed;
  pointer-events: none;
}