/** Widget Opacity */
.ui-fw-models-tree {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.ui-fw-models-tree .core-tree-node.with-checkbox > .contents {
  padding-left: 31px;
}
.ui-fw-models-tree .contents > .core-image-checkbox {
  position: absolute;
  top: unset;
  left: 5px;
  margin: -2px 0px 0px 5px;
}
.ui-fw-models-tree .filteredTreeOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.8);
}
.ui-fw-models-tree .components-tree-errormessage {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 75%;
  text-align: center;
  transform: translate(-50%, -50%);
  font-style: italic;
  color: var(--buic-foreground-muted);
}
.ui-fw-models-tree .components-tree-errormessage .errormessage-header {
  display: block;
  font-weight: bold;
}