.nz-widget-tabTarget {
  position: relative;
  z-index: 22000;
}
.nz-widget-tabTarget.nz-hidden {
  display: none;
}
.nz-widget-tabTarget:after {
  content: "";
  width: 3em;
  height: 100%;
  flex-shrink: 0;
  position: absolute;
  transform: translateX(-50%);
}
.nz-widget-tabTarget.nz-targeted:after {
  background: rgb(140, 210, 255);
  opacity: 0.5;
}