/** Widget Opacity */
.nz-footer-message-layout {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
}
.nz-footer-message-layout > .nz-label {
  grid-row: 1;
  grid-column: 1;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: var(--buic-foreground-body);
}
.nz-footer-message-layout > .nz-buttons {
  grid-row: 1;
  grid-column: 2;
  grid-column-gap: 10px;
  padding-right: 10px;
  display: grid;
  grid-auto-flow: column;
  align-items: center;
}
.nz-footer-message-layout > .nz-buttons > * {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.nz-footer-message-layout > .nz-progress {
  padding: 2px 5px;
  grid-column: 1/3;
  grid-row: 1;
  align-self: end;
}