.nz-zones-zones {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 1;
  visibility: visible;
  pointer-events: none;
  transition: visibility 0.4s ease, opacity 0.4s ease;
}
.nz-zones-zones.nz-hidden {
  opacity: 0;
  visibility: hidden;
}