.uifw-statusbar-sticky-message {
  max-height: 100px;
  transition: max-height 0.5s ease;
}
.uifw-statusbar-sticky-message.uifw-closing {
  max-height: 0;
  opacity: 0;
}
.uifw-statusbar-sticky-message .uifw-statusbar-sticky-close {
  min-width: 35px;
  min-height: 35px;
}