.nz-zones-target-splitter {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.05);
  pointer-events: all;
  cursor: pointer;
  z-index: 7000;
}
.nz-zones-target-splitter:hover, .nz-zones-target-splitter.nz-targeted {
  background-color: rgba(0, 0, 0, 0.3);
}