.iui-non-ideal-state{
  margin:0;
  padding:0;
  border:none;
  vertical-align:baseline;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  text-align:center;
  position:absolute;
  left:50%;
  top:40%;
  min-width:220px;
  transform:translate(-50%, -40%);
}
.iui-non-ideal-state > .iui-non-ideal-state-illustration{
  max-width:220px;
  max-height:242px;
  margin-bottom:22px;
  text-align:center;
  display:flex;
  justify-content:center;
  align-items:center;
}
.iui-non-ideal-state > .iui-non-ideal-state-title{
  margin:0;
  padding:0;
  border:none;
  vertical-align:baseline;
  font-size:32px;
  font-weight:300;
  line-height:44px;
  padding:0;
  margin:0 0 22px;
}
.iui-non-ideal-state > .iui-non-ideal-state-description{
  margin:0;
  padding:0;
  border:none;
  vertical-align:baseline;
  font-size:14px;
  font-weight:400;
  line-height:22px;
  padding:0;
  margin:0 0 22px;
}
.iui-non-ideal-state > .iui-non-ideal-state-actions{
  display:flex;
  align-items:center;
  margin-bottom:8px;
}
.iui-non-ideal-state > .iui-non-ideal-state-actions > .iui-input-container{
  display:inline-block;
  width:200px;
  text-align:left;
  margin:0 4px 0 0;
}
.iui-non-ideal-state > .iui-non-ideal-state-actions > .iui-button{
  margin:0 4px;
}