.nz-footer-snapMode-panel {
  background-color: var(--buic-background-dialog);
  border-radius: 3px;
  overflow: hidden;
  font-size: 14px;
}
.nz-footer-snapMode-panel > .nz-snaps {
  display: grid;
  grid-gap: 8px;
  padding: 8px;
  font-weight: 400;
  font-size: 14px;
}