.nz-footer-indicator {
  height: 100%;
  display: flex;
  align-items: center;
  color: var(--buic-foreground-body);
  font-size: 14px;
  box-sizing: border-box;
  /* Desktop */
  /* Medium devices */
  /* Small devices */
}
.nz-footer-indicator .nz-icon-padding-left {
  padding-left: 4px;
}
.nz-footer-indicator .nz-icon-padding-right {
  padding-right: 4px;
}
.nz-footer-indicator .nz-icon {
  font-size: 16px;
  /* Medium devices */
  /* Small devices */
}
@media (max-width: 1099px) {
  .nz-footer-indicator .nz-icon {
    font-size: 14px;
  }
}
@media (max-width: 599px) {
  .nz-footer-indicator .nz-icon {
    font-size: 14px;
  }
}
@media (min-width: 1300px) {
  .nz-footer-indicator.nz-footer-mode {
    padding-left: 15px;
    padding-right: 15px;
  }
  .nz-footer-indicator:not(.nz-footer-mode) {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media (max-width: 1299px) {
  .nz-footer-indicator.nz-footer-mode {
    padding-left: 10px;
    padding-right: 10px;
  }
  .nz-footer-indicator:not(.nz-footer-mode) {
    padding-left: 3px;
    padding-right: 3px;
  }
}
@media (max-width: 799px) {
  .nz-footer-indicator {
    font-size: 12px;
  }
}
@media (max-width: 599px) {
  .nz-footer-indicator {
    font-size: 12px;
  }
  .nz-footer-indicator.nz-footer-mode {
    padding-left: 5px;
    padding-right: 5px;
  }
  .nz-footer-indicator:not(.nz-footer-mode) {
    padding-left: 2px;
    padding-right: 2px;
  }
}