.rule {
  display: grid;
  grid-template-columns: min-content auto;
  padding: 3px 0;
}
.rule .rule-condition {
  display: grid;
  grid-template-columns: minmax(auto, 500px) 150px minmax(auto, 300px);
  grid-column-gap: 16px;
}
.rule .rule-condition .rule-property .iui-input {
  padding-top: 1.5px;
  padding-bottom: 1.5px;
  min-height: 27px;
}
.rule .rule-remove-action {
  padding-right: 12px;
  visibility: hidden;
}
.rule:focus-within .rule-remove-action, .rule:hover .rule-remove-action {
  visibility: visible;
}

.rule-group-items > .rule {
  padding-bottom: 14px;
}
.rule-group-items > .rule:first-child {
  padding-top: 14px;
}