.uifw-message-renderer {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  top: 0;
  right: 0;
  position: fixed;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  z-index: 16000;
}

@media screen and (min-width: 401px) {
  .uifw-message-renderer {
    min-width: 400px;
    max-width: 640px;
    margin-top: 24px;
  }
}