.nz-target-panelTarget {
  height: 4em;
  width: 4em;
  border: 2px solid var(--buic-background-5);
  border-radius: 3px;
  box-sizing: border-box;
  background-color: var(--buic-background-4);
  transition: transform 0.2s ease-in;
  pointer-events: all;
  display: flex;
  box-shadow: 0 3px 14px hsla(200deg, 12%, 0%, 0.25);
  z-index: 5000;
  animation: nz-grow 0.2s ease-in-out, nz-pulse 3s ease-in-out 2s infinite;
}
@media (prefers-reduced-motion: no-preference) {
  @keyframes nz-pulse {
    from {
      transform: scale3d(1, 1, 1);
    }
    50% {
      transform: scale3d(1.1, 1.1, 1.1);
    }
    to {
      transform: scale3d(1, 1, 1);
    }
  }
  @keyframes nz-grow {
    from {
      transform: scale3d(0, 0, 0);
    }
    to {
      transform: scale3d(1, 1, 1);
    }
  }
}
.nz-target-panelTarget.nz-horizontal {
  width: 6em;
}
.nz-target-panelTarget.nz-vertical {
  height: 6em;
}
.nz-target-panelTarget.nz-hidden {
  display: none;
}
.nz-target-panelTarget.nz-targeted {
  transform: scale(1.2);
  background-color: rgb(140, 210, 255);
  box-shadow: 0 6px 30px hsla(200deg, 12%, 0%, 0.25);
  animation-iteration-count: 0, 0;
}