.tree-widget-search-bar {
  display: flex;
  align-items: center;
  position: relative;
}
.tree-widget-search-bar .search-bar-group-button2 {
  opacity: 0;
  display: none;
  animation: hide ease-in 0.1s;
  animation-fill-mode: forwards;
}
.tree-widget-search-bar .search-bar-group-button2.search-bar-group-button2.show {
  animation: show ease-in 0.25s;
  animation-delay: 0.1s;
  animation-fill-mode: forwards;
  display: block;
}
.tree-widget-search-bar .search-bar-button-container {
  display: flex;
  align-items: center;
}
.tree-widget-search-bar .search-bar-button-container.right {
  justify-content: flex-end;
}
.tree-widget-search-bar .search-bar-button-container.hide {
  display: none;
}
.tree-widget-search-bar .search-bar-dropdown-container {
  display: flex;
  align-items: center;
  padding: 0 8px;
}
.tree-widget-search-bar .search-bar-search-icon {
  position: absolute;
  right: 0;
  opacity: 0;
  transition: all 0.2s ease;
  color: var(--buic-foreground-body);
}
.tree-widget-search-bar .search-bar-search-icon.show {
  opacity: 1;
}
.tree-widget-search-bar .search-bar-search-container {
  flex: 1;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
}
.tree-widget-search-bar .search-bar-search-container .search-bar-search-box {
  height: 32px;
  float: right;
  margin-right: 0;
  box-sizing: border-box;
  width: 0px;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transition: all 0.25s ease;
}
.tree-widget-search-bar .search-bar-search-container .search-bar-search-box.show {
  visibility: visible;
  opacity: 1;
  width: 100%;
}
.tree-widget-search-bar .search-bar-search-container.search-bar-grouping-enabled {
  left: 32px;
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes hide {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}