.core-form-wrapper {
  padding: 8px;
  padding-bottom: 0;
}

.core-form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 14px;
}

.core-form-label {
  padding: 0;
  padding-bottom: 4px;
  margin: 0;
  border: 0 none transparent;
  display: block;
  font-size: 14px;
  line-height: 22px;
  text-align: left;
  color: var(--buic-foreground-body);
}

.core-form-input {
  line-height: 22px;
}

.core-form-textarea {
  line-height: 22px;
}

.core-form-select {
  line-height: 22px;
  height: 27.94px;
  width: fit-content;
  min-width: 20%;
}

.core-form-footer {
  flex-shrink: 0;
  padding-bottom: 0;
}
.core-form-footer > .core-form-buttons {
  display: flex;
  justify-content: flex-end;
}
.core-form-footer > .core-form-buttons > * {
  min-width: 80px;
  font-size: 16px;
}
.core-form-footer > .core-form-buttons > *:not(:first-child) {
  margin-left: 4.4px;
}

.core-form-alert {
  padding: 0.75rem;
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  color: var(--buic-accessory-alert);
  background-color: var(--buic-accessory-alert-tint);
}