div.components-timeline-thumb.iui-slider-thumb {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to bottom, var(--buic-background-3), var(--buic-background-3)), linear-gradient(to bottom, var(--buic-background-1), var(--buic-background-1));
  box-sizing: border-box;
  width: 38px;
  height: 20px;
  border-radius: 4px;
  border: 1px solid var(--buic-background-5);
  transform: translateX(-19.2px);
  top: 4px;
}

.components-timeline-rail-marker-container {
  width: 100%;
  position: absolute;
}
.components-timeline-rail-marker-container div.components-timeline-tooltip {
  z-index: 18000;
  position: absolute;
  top: -8px;
  display: inline-block;
  transform: translateX(-50px);
  border-bottom: 1px dotted #222;
  font-size: 14px;
}
.components-timeline-rail-marker-container div.components-timeline-tooltip .tooltip-text {
  width: 100px;
  background-color: #222;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  bottom: 150%;
}
.components-timeline-rail-marker-container div.components-timeline-tooltip .tooltip-text:after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #222 transparent transparent transparent;
}

.scrubber-handle {
  pointer-events: none;
  position: absolute;
  width: 38px;
  height: 20px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  cursor: pointer;
}
.scrubber-handle > div {
  width: 1px;
  height: 10px;
  opacity: 0.25;
  margin: 0 1px;
  background-color: var(--buic-foreground-body);
}

.scrubber-track {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 4px;
  z-index: 1;
  cursor: pointer;
}

.components-timeline-date-marker {
  pointer-events: none;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(50%);
}

.date-marker-default {
  height: 12px;
  width: 3px;
  border-radius: 4px;
  transform: translateY(1px);
  background-color: var(--buic-foreground-primary);
}