.property-grid-widget-container {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.pcomponents-property-grid-with-unified-selection {
  overflow: hidden !important;
}

.property-grid-container {
  flex: 1;
  overflow: hidden;
}

.virtualized-grid-node-content > .components--hoverable:hover {
  background: var(--buic-row-hover) !important;
}

.property-grid-react-panel-label-and-class {
  display: block;
  overflow: hidden;
}

.property-grid-react-panel-label {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--buic-foreground-body);
  font-size: 16px;
  padding: 4px 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.property-grid-react-panel-info-btn {
  cursor: pointer;
  margin-left: auto;
  display: flex;
  color: var(--buic-foreground-body);
  padding: 4px;
  height: 100%;
}
.property-grid-react-panel-info-btn:hover {
  color: var(--buic-foreground-primary);
}

.property-grid-react-panel-back-btn {
  cursor: pointer;
  padding: 4px;
  display: flex;
  color: var(--buic-foreground-body);
  margin-right: 2px;
}
.property-grid-react-panel-back-btn:hover {
  color: var(--buic-foreground-primary);
}

.property-grid-react-panel-icon {
  font-size: 24px;
  margin: auto;
}

.property-grid-react-panel-header {
  display: flex;
  margin-right: 6px;
  height: 50px;
  box-sizing: border-box;
}

.property-grid-react-panel-class {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: var(--buic-foreground-body);
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.property-grid-react-filtering-pg-label {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: var(--buic-foreground-body);
  font-size: 14px;
  text-align: center;
  padding: 0 16px;
}

.property-grid-react-filtering-pg-icon {
  font-size: 64px;
  color: RGBA(var(--buic-foreground-body-rgb), 0.3);
  margin-bottom: 16px;
}