  display: block;
  top: 6px;
  left: 3px;
  width: 7px;
  border-width: 0 1px 2px 0;
}
.uicore-inputs-checkbox.core-disabled {
  opacity: var(--iui-opacity-4);
  cursor: not-allowed;
}
.uicore-inputs-checkbox.success {
