.presentation-components-muted-node {
  color: rgba(0, 0, 0, 0.4);
  color: var(--iui-text-color-muted);
}

.presentation-components-node .presentation-components-filter-action-button {
  visibility: hidden;
  position: sticky;
  background-color: inherit;
  right: 0;
  display: flex;
}
.presentation-components-node .presentation-components-filter-action-button .iui-button {
  height: 24px;
  width: 24px;
}
.presentation-components-node:hover > .presentation-components-filter-action-button {
  visibility: visible;
}