.components-filtering-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
}

.components-filtering-input-input {
  position: relative;
  flex: 1;
}
.components-filtering-input-input input {
  min-height: 28px;
  padding: 2px 8px;
  box-sizing: border-box;
}
.components-filtering-input-input input::-ms-clear {
  display: none;
}

.components-filtering-input-input-components {
  display: flex;
  position: absolute;
  right: 8px;
  top: 1px;
  bottom: 0px;
}
.components-filtering-input-input-components > .icon {
  align-self: center;
  border: none;
  outline: none;
}

.components-filtering-input-button {
  --_iui-button-active-stripe-inset: initial;
  --_iui-button-text-color: rgba(var(--iui-color-foreground-body-rgb), var(--iui-opacity-2));
  margin: 0;
  padding: 0;
  border: none;
  vertical-align: baseline;
  font-family: inherit;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  position: relative;
  box-sizing: border-box;
  border-radius: 4px;
  line-height: 22px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  user-select: none;
  cursor: pointer;
  white-space: nowrap;
  border: 1px solid transparent;
  color: rgba(0, 0, 0, 0.8);
  color: rgba(var(--iui-color-foreground-body-rgb), var(--iui-opacity-2));
  color: var(--_iui-button-text-color);
  background-color: hsl(0deg, 0%, 100%);
  border-color: rgba(0, 0, 0, 0.4);
  background-color: var(--iui-color-background-1);
  border-color: rgba(var(--iui-color-foreground-body-rgb), var(--iui-opacity-4));
  margin-left: 6px;
}
@media (prefers-reduced-motion: no-preference) {
  .components-filtering-input-button {
    transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out;
  }
}
.components-filtering-input-button:hover {
  --_iui-button-text-color: rgba(var(--iui-color-foreground-body-rgb), var(--iui-opacity-1));
  text-decoration: none;
}
.components-filtering-input-button:focus-visible {
  outline: 1px solid var(--iui-color-foreground-primary);
  outline-offset: -1px;
}
@supports not selector(*:focus-visible) {
