/** Widget Opacity */
.components-color-swatch {
  font-size: 14px;
  height: 1.2em;
  width: 1.2em;
  border-width: 1px;
  border-radius: 3px;
  border-style: solid;
  border-color: var(--buic-inputs-border);
}
.components-color-swatch.round {
  border-radius: 50%;
}
.components-color-swatch[disabled] {
  opacity: 0.2;
}