/** Widget Opacity */
.uifw-position-popup {
  border-color: var(--buic-background-panel-stroke);
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  background-color: rgba(var(--buic-background-2-rgb), 0.8);
  position: fixed;
  height: fit-content;
  width: fit-content;
}
.uifw-position-popup .uifw-position-popup-content {
  padding: 5px;
}
.uifw-position-popup.uifw-no-border {
  border-width: 0;
}